/* Navigation styling */
body header {
    width: 200px;
    width: 100px;
    cursor: pointer;
  }
  
  body header h1,
  nav li {
    text-transform: uppercase;
  }
  
  body header nav li {
    margin: 10% 5%;
    border-bottom: 2px solid lightgray;
  }
  
  body header nav li:hover {
    border-bottom-color: #45e9f5;
  }